'use client';

import { useState, useEffect } from 'react';
import Link from 'next/link';
import { 
  ArrowRightIcon, 
  UserGroupIcon, 
  ChartBarIcon,
  UsersIcon,
  CubeIcon,
  ShieldCheckIcon,
  SparklesIcon,
  RocketLaunchIcon,
} from '@heroicons/react/24/outline';
import { useRouter } from 'next/navigation';
import { useSession } from 'next-auth/react';

type DashboardStats = {
  employeeCount: number;
  productCount: number;
};

const quickActions = [
  { 
    name: 'Manage Employees', 
    href: '/admin/employees', 
    icon: UserGroupIcon,
    description: 'View and manage employee information and schedules',
    color: 'bg-pink-100 text-pink-600',
    hoverColor: 'hover:bg-pink-600',
    delay: 'delay-0',
  },
  { 
    name: 'Manage Products', 
    href: '/admin/products', 
    icon: ChartBarIcon,
    description: 'View and manage your product listings',
    color: 'bg-amber-100 text-amber-600',
    hoverColor: 'hover:bg-amber-600',
    delay: 'delay-100',
  },
];

export default function AdminDashboard() {
  const router = useRouter();
  const { status } = useSession();
  const [stats, setStats] = useState<DashboardStats | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [mousePosition, setMousePosition] = useState({ x: 0, y: 0 });

  useEffect(() => {
    if (status === 'loading') return;
    
    if (status === 'unauthenticated') {
      router.push('/login');
      return;
    }

    const fetchStats = async () => {
      try {
        const response = await fetch('/api/dashboard/stats');
        if (response.ok) {
          const data = await response.json();
          setStats(data);
        }
      } catch (error) {
        console.error('Error fetching stats:', error);
      } finally {
        setIsLoading(false);
      }
    };

    fetchStats();
  }, [status, router]);

  // Track mouse position for 3D effects
  useEffect(() => {
    const handleMouseMove = (e: MouseEvent) => {
      setMousePosition({ x: e.clientX, y: e.clientY });
    };

    window.addEventListener('mousemove', handleMouseMove);
    return () => window.removeEventListener('mousemove', handleMouseMove);
  }, []);

  if (status === 'loading' || isLoading) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-orange-50 via-pink-50 to-purple-50 relative overflow-hidden">
        {/* Animated Background Elements */}
        <div className="absolute inset-0 overflow-hidden pointer-events-none">
          <div className="absolute -top-40 -right-40 w-80 h-80 bg-gradient-to-br from-orange-400/20 to-pink-400/20 rounded-full blur-3xl animate-float"></div>
          <div className="absolute -bottom-40 -left-40 w-80 h-80 bg-gradient-to-br from-purple-400/20 to-blue-400/20 rounded-full blur-3xl animate-float-delayed"></div>
        </div>
        
        <div className="container mx-auto px-6 py-8 relative z-10">
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-8">
            {[...Array(4)].map((_, i) => (
              <div key={i} className="bg-white/80 backdrop-blur-sm rounded-2xl shadow-2xl h-32 animate-pulse transform hover:scale-105 transition-all duration-300"></div>
            ))}
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-orange-50 via-pink-50 to-purple-50 relative overflow-hidden">
      {/* Enhanced Animated Background Elements */}
      <div className="absolute inset-0 overflow-hidden pointer-events-none">
        <div className="absolute -top-40 -right-40 w-80 h-80 bg-gradient-to-br from-orange-400/20 to-pink-400/20 rounded-full blur-3xl animate-float"></div>
        <div className="absolute -bottom-40 -left-40 w-80 h-80 bg-gradient-to-br from-purple-400/20 to-blue-400/20 rounded-full blur-3xl animate-float-delayed"></div>
        <div className="absolute top-1/4 left-1/4 w-60 h-60 bg-gradient-to-br from-yellow-400/15 to-orange-400/15 rounded-full blur-2xl animate-float-slow"></div>
        <div className="absolute bottom-1/4 right-1/4 w-40 h-40 bg-gradient-to-br from-pink-400/15 to-purple-400/15 rounded-full blur-2xl animate-float-reverse"></div>
        
        {/* Particle Effects */}
        <div className="absolute inset-0">
          {[...Array(20)].map((_, i) => (
            <div
              key={i}
              className="absolute w-2 h-2 bg-gradient-to-r from-orange-400 to-pink-400 rounded-full animate-particle"
              style={{
                left: `${Math.random() * 100}%`,
                top: `${Math.random() * 100}%`,
                animationDelay: `${Math.random() * 5}s`,
                animationDuration: `${3 + Math.random() * 4}s`,
              }}
            />
          ))}
        </div>
      </div>

      <div className="container mx-auto px-6 py-8 relative z-10">
        {/* Enhanced Header */}
        <div className="mb-12 text-center animate-fade-in-up">
          <div className="relative inline-block">
            <div className="absolute inset-0 bg-gradient-to-r from-orange-600 via-pink-600 to-purple-600 rounded-full blur-2xl opacity-50 animate-pulse"></div>
            <h1 className="relative text-5xl font-bold bg-gradient-to-r from-orange-600 via-pink-600 to-purple-600 bg-clip-text text-transparent mb-4 animate-gradient">
              Welcome to Dunkin' Dashboard
            </h1>
          </div>
          <div className="flex items-center justify-center gap-2 mb-4">
            <SparklesIcon className="h-6 w-6 text-orange-500 animate-bounce" />
            <p className="text-xl text-gray-600 max-w-2xl mx-auto leading-relaxed">
              Your command center for managing employees and products
            </p>
            <SparklesIcon className="h-6 w-6 text-purple-500 animate-bounce" style={{ animationDelay: '0.5s' }} />
          </div>
        </div>

        {/* Enhanced Stats Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 gap-8 mb-12">
          {/* Total Employees Card */}
          <div className="group relative perspective-1000">
            <div className="absolute inset-0 bg-gradient-to-br from-orange-400 to-pink-500 rounded-3xl blur-lg opacity-75 group-hover:opacity-100 transition-all duration-500 animate-float"></div>
            <div 
              className="relative bg-white/90 backdrop-blur-xl rounded-3xl p-8 shadow-2xl border border-white/20 hover:shadow-3xl transition-all duration-500 hover:scale-105 hover:-translate-y-2 transform-gpu transform-style-preserve-3d"
              style={{
                transform: `rotateY(${(mousePosition.x - window.innerWidth / 2) * 0.01}deg) rotateX(${(mousePosition.y - window.innerHeight / 2) * -0.01}deg)`,
              }}
            >
              <div className="flex items-center justify-between mb-4">
                <div className="p-4 bg-gradient-to-br from-orange-500 to-pink-500 rounded-2xl shadow-lg transform rotate-3 hover:rotate-0 transition-transform duration-300 hover:scale-110">
                  <UsersIcon className="h-8 w-8 text-white" />
                </div>
                <div className="text-right">
                  <p className="text-sm font-medium text-gray-600 mb-1">Total Employees</p>
                  <p className="text-3xl font-bold bg-gradient-to-r from-orange-600 to-pink-600 bg-clip-text text-transparent animate-count-up">
                    {stats?.employeeCount || 0}
                  </p>
                </div>
              </div>
              <div className="h-2 bg-gradient-to-r from-orange-200 to-pink-200 rounded-full overflow-hidden">
                <div 
                  className="h-full bg-gradient-to-r from-orange-500 to-pink-500 rounded-full transition-all duration-1000 ease-out animate-progress"
                  style={{ width: `${Math.min((stats?.employeeCount || 0) * 10, 100)}%` }}
                ></div>
              </div>
              <div className="absolute inset-0 bg-gradient-to-br from-orange-400/10 to-pink-400/10 rounded-3xl opacity-0 group-hover:opacity-100 transition-opacity duration-300"></div>
            </div>
          </div>

          {/* Total Products Card */}
          <div className="group relative perspective-1000">
            <div className="absolute inset-0 bg-gradient-to-br from-purple-400 to-blue-500 rounded-3xl blur-lg opacity-75 group-hover:opacity-100 transition-all duration-500 animate-float-delayed"></div>
            <div 
              className="relative bg-white/90 backdrop-blur-xl rounded-3xl p-8 shadow-2xl border border-white/20 hover:shadow-3xl transition-all duration-500 hover:scale-105 hover:-translate-y-2 transform-gpu transform-style-preserve-3d"
              style={{
                transform: `rotateY(${(mousePosition.x - window.innerWidth / 2) * 0.01}deg) rotateX(${(mousePosition.y - window.innerHeight / 2) * -0.01}deg)`,
              }}
            >
              <div className="flex items-center justify-between mb-4">
                <div className="p-4 bg-gradient-to-br from-purple-500 to-blue-500 rounded-2xl shadow-lg transform -rotate-3 hover:rotate-0 transition-transform duration-300 hover:scale-110">
                  <CubeIcon className="h-8 w-8 text-white" />
                </div>
                <div className="text-right">
                  <p className="text-sm font-medium text-gray-600 mb-1">Total Products</p>
                  <p className="text-3xl font-bold bg-gradient-to-r from-purple-600 to-blue-600 bg-clip-text text-transparent animate-count-up">
                    {stats?.productCount || 0}
                  </p>
                </div>
              </div>
              <div className="h-2 bg-gradient-to-r from-purple-200 to-blue-200 rounded-full overflow-hidden">
                <div 
                  className="h-full bg-gradient-to-r from-purple-500 to-blue-500 rounded-full transition-all duration-1000 ease-out animate-progress"
                  style={{ width: `${Math.min((stats?.productCount || 0) * 10, 100)}%` }}
                ></div>
              </div>
              <div className="absolute inset-0 bg-gradient-to-br from-purple-400/10 to-blue-400/10 rounded-3xl opacity-0 group-hover:opacity-100 transition-opacity duration-300"></div>
            </div>
          </div>
        </div>

        {/* Enhanced Quick Actions */}
        <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
          {/* Employees Section */}
          <div className="group relative perspective-1000">
            <div className="absolute inset-0 bg-gradient-to-br from-orange-400/20 to-pink-400/20 rounded-3xl blur-xl opacity-75 group-hover:opacity-100 transition-all duration-500"></div>
            <div 
              className="relative bg-white/80 backdrop-blur-xl rounded-3xl p-8 shadow-2xl border border-white/20 hover:shadow-3xl transition-all duration-500 hover:scale-105 transform-gpu transform-style-preserve-3d"
              style={{
                transform: `rotateY(${(mousePosition.x - window.innerWidth / 2) * 0.005}deg) rotateX(${(mousePosition.y - window.innerHeight / 2) * -0.005}deg)`,
              }}
            >
              <div className="flex items-center mb-6">
                <div className="p-4 bg-gradient-to-br from-orange-500 to-pink-500 rounded-2xl shadow-lg mr-4 transform rotate-3 hover:rotate-0 transition-transform duration-300 hover:scale-110">
                  <UsersIcon className="h-8 w-8 text-white" />
                </div>
                <div>
                  <h3 className="text-2xl font-bold bg-gradient-to-r from-orange-600 to-pink-600 bg-clip-text text-transparent">
                    Manage Employees
                  </h3>
                  <p className="text-gray-600">Add, edit, and manage your team</p>
                </div>
              </div>
              <button
                onClick={() => router.push('/admin/employees')}
                className="w-full bg-gradient-to-r from-orange-500 to-pink-500 hover:from-orange-600 hover:to-pink-600 text-white font-semibold py-4 px-6 rounded-2xl shadow-lg hover:shadow-xl transition-all duration-300 hover:scale-105 transform-gpu hover:-translate-y-1 relative overflow-hidden group"
              >
                <span className="relative z-10 flex items-center justify-center gap-2">
                  <RocketLaunchIcon className="h-5 w-5" />
                  View Employees
                </span>
                <div className="absolute inset-0 bg-gradient-to-r from-orange-600 to-pink-600 opacity-0 group-hover:opacity-100 transition-opacity duration-300"></div>
              </button>
            </div>
          </div>

          {/* Products Section */}
          <div className="group relative perspective-1000">
            <div className="absolute inset-0 bg-gradient-to-br from-purple-400/20 to-blue-400/20 rounded-3xl blur-xl opacity-75 group-hover:opacity-100 transition-all duration-500"></div>
            <div 
              className="relative bg-white/80 backdrop-blur-xl rounded-3xl p-8 shadow-2xl border border-white/20 hover:shadow-3xl transition-all duration-500 hover:scale-105 transform-gpu transform-style-preserve-3d"
              style={{
                transform: `rotateY(${(mousePosition.x - window.innerWidth / 2) * 0.005}deg) rotateX(${(mousePosition.y - window.innerHeight / 2) * -0.005}deg)`,
              }}
            >
              <div className="flex items-center mb-6">
                <div className="p-4 bg-gradient-to-br from-purple-500 to-blue-500 rounded-2xl shadow-lg mr-4 transform -rotate-3 hover:rotate-0 transition-transform duration-300 hover:scale-110">
                  <CubeIcon className="h-8 w-8 text-white" />
                </div>
                <div>
                  <h3 className="text-2xl font-bold bg-gradient-to-r from-purple-600 to-blue-600 bg-clip-text text-transparent">
                    Manage Products
                  </h3>
                  <p className="text-gray-600">Add, edit, and manage your products</p>
                </div>
              </div>
              <button
                onClick={() => router.push('/admin/products')}
                className="w-full bg-gradient-to-r from-purple-500 to-blue-500 hover:from-purple-600 hover:to-blue-600 text-white font-semibold py-4 px-6 rounded-2xl shadow-lg hover:shadow-xl transition-all duration-300 hover:scale-105 transform-gpu hover:-translate-y-1 relative overflow-hidden group"
              >
                <span className="relative z-10 flex items-center justify-center gap-2">
                  <RocketLaunchIcon className="h-5 w-5" />
                  View Products
                </span>
                <div className="absolute inset-0 bg-gradient-to-r from-purple-600 to-blue-600 opacity-0 group-hover:opacity-100 transition-opacity duration-300"></div>
              </button>
            </div>
          </div>
        </div>
      </div>

      <style jsx>{`
        @keyframes float {
          0%, 100% { transform: translateY(0px) rotate(0deg); }
          50% { transform: translateY(-20px) rotate(5deg); }
        }
        
        @keyframes float-delayed {
          0%, 100% { transform: translateY(0px) rotate(0deg); }
          50% { transform: translateY(-15px) rotate(-5deg); }
        }
        
        @keyframes float-slow {
          0%, 100% { transform: translateY(0px) rotate(0deg); }
          50% { transform: translateY(-10px) rotate(3deg); }
        }
        
        @keyframes float-reverse {
          0%, 100% { transform: translateY(0px) rotate(0deg); }
          50% { transform: translateY(10px) rotate(-3deg); }
        }
        
        @keyframes gradient {
          0% { background-position: 0% 50%; }
          50% { background-position: 100% 50%; }
          100% { background-position: 0% 50%; }
        }
        
        @keyframes particle {
          0% { transform: translateY(0px) scale(0); opacity: 0; }
          50% { opacity: 1; }
          100% { transform: translateY(-100px) scale(1); opacity: 0; }
        }
        
        @keyframes count-up {
          0% { transform: scale(0.8); opacity: 0; }
          100% { transform: scale(1); opacity: 1; }
        }
        
        @keyframes progress {
          0% { width: 0%; }
          100% { width: var(--progress-width); }
        }
        
        .animate-float {
          animation: float 6s ease-in-out infinite;
        }
        
        .animate-float-delayed {
          animation: float-delayed 8s ease-in-out infinite;
        }
        
        .animate-float-slow {
          animation: float-slow 10s ease-in-out infinite;
        }
        
        .animate-float-reverse {
          animation: float-reverse 12s ease-in-out infinite;
        }
        
        .animate-gradient {
          background-size: 200% 200%;
          animation: gradient 3s ease infinite;
        }
        
        .animate-particle {
          animation: particle 5s linear infinite;
        }
        
        .animate-count-up {
          animation: count-up 0.5s ease-out;
        }
        
        .animate-progress {
          animation: progress 1s ease-out;
        }
        
        .shadow-3xl {
          box-shadow: 0 35px 60px -12px rgba(0, 0, 0, 0.25);
        }
        
        .backdrop-blur-xl {
          backdrop-filter: blur(24px);
        }
        
        .perspective-1000 {
          perspective: 1000px;
        }
        
        .transform-style-preserve-3d {
          transform-style: preserve-3d;
        }
        
        .animate-fade-in-up {
          animation: fadeInUp 0.8s ease-out;
        }
        
        @keyframes fadeInUp {
          0% {
            opacity: 0;
            transform: translateY(30px);
          }
          100% {
            opacity: 1;
            transform: translateY(0);
          }
        }
      `}</style>
    </div>
  );
}
